import React from 'react'
import { Redirect } from 'react-router';
import axios from 'axios'
import './RepairForm.css'

class RepairForm extends React.Component{
    constructor(props){
        super(props)
        this.state = {values:{name:null,email:null,tel:null,tel1:null,device:null,brand:null,usageTime:null,shortDesc:null,description:null},redirect:false}

        this.handleChange = this.handleChange.bind(this)
        this.handleSubmit = this.handleSubmit.bind(this)
    }


    handleChange(keyName,e){
        var property = {...this.state.values}
        property[keyName] = e.target.value

        this.setState({values:property})
    }

    handleSubmit(e){
        e.preventDefault()

        axios({
            url:'/saveCallService',
            method: 'POST',
            data: this.state.values
        }).then(() => {
            this.setState({redirect:true})
        }).catch(() => {
            
        })

    }

    render(){
        return(
            <>
            {this.state.redirect ? <Redirect to={{pathname: "/", state: this.state.redirect}} /> : null}
            <div className="center">
                <h4>Contratação de serviços</h4>
            </div>
            <div className="contentsRepair">
                <form onSubmit={this.handleSubmit} className="formRepair" spellCheck="false">
                    <div className="sectionFormTitle">
                        <h2>Dados para contato</h2>
                    </div>
                    <div className="boxFormRepair">
                        <label>Nome Completo:</label>
                        <input type="text" onChange={(e) => this.handleChange('name', e)} placeholder="Thiago Comelli" required></input>
                    </div>
                    <div className="boxFormRepair">
                        <label>Email:</label>
                        <input type="email" onChange={(e) => this.handleChange('email',e)} placeholder="thiago.comelli@outlook.com" required></input>
                    </div>
                    <div className="boxFormRepairDuplo">
                        <div className="boxFormRepairDuploItem">
                            <label>Telefone:</label>
                            <input type="tel" onChange={(e) => this.handleChange('tel',e)} placeholder="(48)99603-0226" required></input>
                        </div>
                        <div className="boxFormRepairDuploItem">
                            <label>Telefone alternativo:</label>
                            <input type="tel" onChange={(e) => this.handleChange('tel1',e)} placeholder="(xx)xxxx-xxxx" required></input>
                        </div>
                    </div>
                    <div className="sectionFormTitle">
                        <h2>Descrição do serviço</h2>
                    </div>
                    <div className="boxFormRepairDuplo">
                        <div className="boxFormRepairDuploItem">
                            <label>Tipo de dispositivo:</label>
                            <input type="text" onChange={(e) => this.handleChange('device',e)} placeholder="Celular" required></input>
                        </div>
                        <div className="boxFormRepairDuploItem">
                            <label>Marca/modelo:</label>
                            <input type="text" onChange={(e) => this.handleChange('brand',e)} placeholder="Samsung Galaxy S20" required></input>
                        </div>
                        <div className="boxFormRepairDuploItem">
                            <label>Tempo de uso:</label>
                            <input type="text" onChange={(e) => this.handleChange('usageTime',e)} placeholder="2 anos e 3 meses" required></input>
                        </div>
                        <div className="boxFormRepairDuploItem">
                            <label>Breve descrição:</label>
                            <input type="text" onChange={(e) => this.handleChange('shortDesc',e)} placeholder="Bateria viciada" required></input>
                        </div>
                        <div className="boxFormRepair">
                            <label>Descrição detalhada:</label>
                            <textarea rows="6" onChange={(e) => this.handleChange('description',e)} required></textarea>
                        </div>
                    </div>
                    <div className="boxFormRepair">
                        <button type="submit">Enviar</button>
                    </div>
                </form>
            </div>
            </>
        )
    }
}

export default RepairForm