import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import './Navbar.css'

class Navbar extends Component{
    collapse () {
        var div = document.getElementsByClassName("menu")[0]
        var button = document.getElementsByClassName("collapse")[0]
    
        if(div.style.display === "none" || div.style.display === ""){
            div.style.display = "block"
            button.textContent = "Fechar menu"
        } else {
            div.style.display = "none"
            button.textContent = "Menu"
        }
    }

    render(){
        return (
            <>
            <nav className="nav" id="nav">
                {/* <a href="/" >
                    <img src="images/apple.png" alt="Apple Logo"/>
                </a> */}
                <Link to="/">
                    <img src={`${process.env.PUBLIC_URL}/images/apple.png`} alt="Apple Logo"/>
                </Link>
                <button className="collapse" onClick={this.collapse}>Menu</button>
                <ul className="navlinks">
                    <li><Link to="#">Produtos</Link></li>
                    <li><Link to="/services">Serviços</Link></li>
                    <li><Link to="#">Contato</Link></li>
                </ul>
                <div className="menu">
                    <div><a href="/#">Produtos</a></div>
                    <div><a href="/#">Serviços</a></div>
                    <div><a href="/#">Contato</a></div>
                </div>
            </nav>
            <div className="products">
                <ul className="navlinks">
                    <li><a href="/#">Mac</a></li>
                    <li><a href="/#">iPad</a></li>
                    <li><a href="/#">iPhone</a></li>
                </ul>
            </div>
            </>
        )
    }
}

export default Navbar